.btn-default {
     font-size: 20px;
     padding: .75em;
     border-radius: 5px;
     white-space: nowrap;
     border: 2px solid #000;
     transition: all 0.2s ease 0s;

     &:hover {
          transform: scale(1.1);
     }

     @media (max-width:895px) {
          align-self: end;

     }
}

.btn-yellow {
     background-color: #FBFF22;
     border: 2px solid #FBFF22;
     color: #000;
     transition: all 0.3s ease 0s;

     &:hover {
          box-shadow: 0px 4px 15px 4px #636504 !important;
     }

     @media (min-width:481px) {
          align-self: start;
     }

}

.btn-white-border {
     border: 2px solid #fff;
}

.btn-pseudo {
     position: relative;



     &::before {
          content: "";
          display: inline-block;
          width: 16px;
          height: 16px;
          margin: 0px 10px 0px 0px;
          pointer-events: none;
          transform: translateY(-8%);
          background: url("../../assets/icons/mainPage/file-text-fill.svg") center/contain no-repeat;
          vertical-align: middle;
     }
}

.btn-cart-btn {
     @media (max-width:920px) {
          font-size: 15px;
          padding: .3em;
     }

     @media (max-width:730px) {
          width: 100%;
          align-self: center
     }
}

.btn-yell-border {
     border: 2px solid #FBFF22;
     color:#FBFF22;
     align-self: center;

     &:hover {
          box-shadow: 0 15px 50px 5px #FBFF22
     }
}